#page {
    padding: 2em;
}

h1 {
    margin-bottom: 1em;
}

#page-wrapper {
    max-width: 66%;
    margin: 0 auto 0 auto;
    overflow: auto
}

#messages {
    background-color: rgb(230, 230, 230);
    margin: 1em 0;
    padding: 1em;
}

@font-face {
    font-family: 'Inter';
    src:  url('./fonts/static/Inter-Regular.ttf') format('ttf'),
    url('./fonts/static/Inter-Medium.ttf') format('ttf');
}

@import url('https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap');

body, html {
    margin: 0;
    font-family: 'Patrick Hand', cursive;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100%;
    /*font-size: 12px;
    font-family: 'Dancing Script', cursive;*/
}

.image {
  background-image: url('https://cdn.pixabay.com/photo/2020/04/11/11/45/background-5030087_960_720.jpg');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  height: 100%;
  width: 100%;
  background-position: center;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  -moz-filter: blur(5px);
  -o-filter: blur(5px);
  -ms-filter: blur(5px);
  position: fixed;
  top:0;
}

#App {
  z-index: 2;
  position: absolute;  
  top: 0%;
  left: 0%;
  height: 100%;
  width: 100%;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

span.important {
    font-size: 1.9rem;
    color: #FF585D;
}

.claim-inputs {
    margin-top: 30px
}

.claim-inputs label {
    font-size: 1.3em;
}

#claim-memo, #claim-account-id, .btn {
    border-radius: 5px;
    border: .1em solid #000;
    padding: .5em 1em;
    font-size: 1.66em;
    margin: .5em;
}

.claim-inputs .btn {
    margin: .5em;
}

#page h1, #page.no-puzzles {
    text-align: center;
}

#page.claim {
    margin: 0 auto;
    width: 576px;
}

.margin_button {
    margin-right: 10px;
    margin-top: 5px;
    margin-bottom: 5px;
}

.login_button {
    margin-left: 10px;
    font-size: 1.3em;
}

.name_input {
    margin-left: 10px;
}

.bm-item {
  display: inline-block;
  text-decoration: none;
  margin-bottom: 10px;
  color: #d1d1d1;
  transition: color 0.2s;
}

.bm-item:hover {
  color: white;
}

.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 36px;
  top: 36px;
}

.bm-burger-bars {
  background: #373a47;
}

.bm-cross-button {
  height: 24px;
  width: 24px;
}

.bm-cross {
  background: #bdc3c7;
}

.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

nav {
  background: transparent;
  box-shadow: none;
}

.bm-morph-shape {
  fill: #373a47;
}

.bm-item-list {
  color: #b8b7ad;
}

.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

.btn-login {
  position: fixed;
  top: 10px;
  right: 10px;
}

/* Popup style */
.popup-box {
  position: fixed;
  background: #00000050;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}

.box {
  position: relative;
  width: 70%;
  margin: 0 auto;
  height: auto;
  max-height: 70vh;
  margin-top: calc(100vh - 85vh - 20px);
  background: #fff;
  border-radius: 4px;
  padding: 20px;
  border: 1px solid #999;
  overflow: auto;
}

.close-icon {
  content: 'x';
  cursor: pointer;
  position: fixed;
  right: calc(15% - 30px);
  top: calc(100vh - 85vh - 33px);
  background: #ededed;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  line-height: 20px;
  text-align: center;
  border: 1px solid #999;
  font-size: 20px;
}

.nft-image {
  object-fit: contain;
}

.card-stats .card-body .numbers{
  text-align: right;
}

.card-stats{
  padding-left: 10px;
  padding-right: 10px;
}

.card-stats .icon-big {
  font-size: 3em;
  min-height: 64px;
}

.card-image {
  padding-top: 10px;
}

.card {
  background-color: #ffffffaa;
}

.text-center {
  text-align: center!important;
}

p.small {
  margin: 0;
}

form {
  background-color: #ffffffaa;
}

fieldset {
  margin:0;
}